import React, { Component } from 'react';

class Upload extends Component {
  render() {
    return(
      <div>
        <form action="/apis/posts" method="post" encType="multipart/form-data">
          <table className="table table-bordered">
            <tbody>
            <tr>
                <th>제목</th>
                <td><input type="text" name="title" className="form-control" /></td>
            </tr>
            <tr>
                <th>내용</th>
                <td><input type="text" name="content" className="form-control" /></td>
            </tr>
            <tr>
            <th>섬네일</th>
                <td>
                    <input type="file" name="thumbnail" />
                </td>
            </tr>
            </tbody>
        </table>
        <div>
            <button className="btn btn-default">작성하기</button>
        </div>
        </form>
      </div>
    );
  }
}

module.exports = Upload;



